<template>
    <div class="DrawerMessage3">
      <div class="hint" >
        温馨提示：只有已确认收货并且含税的订单可以申请发票，累计200元即可开普票，累计1000元即可开专票；不满金额，每月底20-25号申请，次月初开具发票。
        <a href="###">查看帮助</a>
      </div>
      <van-collapse v-model="activeNames" class="vantcollapse">
        <van-collapse-item title="编号" :name=index  :value=data.text class="mt24" v-for="(data,index) in TallageData" :key="index">
          <template #title>
            <div @click.stop="SelectHe(data,index)"> <i class="icons" :class="data.select==true?'bg_icon':'bg_icons'"></i> <span class="vm f34">订单包编号</span></div>
          </template>
          <div>
            <div class="specific">
              <p>
                <span>金额：</span>
                <span>${{data.jine}}</span>
              </p>
              <p>
                <span>类型 ：</span>
                <span>{{data.leixing}}</span>
              </p>
              <p>
                <span>支付方式 ：</span>
                <span>{{data.fangshi}}</span>
              </p>
              <p>
                <span>账号：</span>
                <span>{{data.zhanghao}}</span>
              </p>
              <p>
                <span>申请时间：</span>
                <span>{{data.shijian}}</span>
              </p>
              <p>
                <span>状态：</span>
                <span>{{data.zhaungtai}}</span>
              </p>
            </div>
            <div class="remove">
              <p >删除</p>
            </div>
          </div>
        </van-collapse-item>
      </van-collapse>
      <div class="both bottomBase" >
        <router-link  class=" nextStep"  :to="{path:'FillMessage'}">下一步</router-link>
          <div  >
          <span class="cl_999 f24">选中{{money}}条</span>
          <span class="f28 cl_333">可开票金额:</span>
          <span class="f34 cl_f90">${{AggregateAmount}}</span>
          </div>
       </div>
    </div>
</template>

<script>
    import Vue from "vue"

    export default {
        name: "DrawerMessage3",
        components: {},
        data() {
            return {
              activeNames: ['1'],
              TallageData1:[
                {name:true,text:"aaaaaa",danwei:"捷配1",kaihuhang:"招商银行1",zhanghao:"17637408019",dizhi:'瞎胡层序亲天禧1',dianhua:"17637408019"},
              ],
              TallageData:[
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:true},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:true},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:false},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:true},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:false},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:true},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:false},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:false},
                {name:true,text:"123456123456123",jine:"1.00",leixing:"增值税专用发票",fangshi:"微信支付",zhanghao:'17637408019',shijian:"2020.02.25",zhaungtai:"已经完成",select:true},
              ],
              //总金额
              AggregateAmount:"",
              money:0,
            }
        },
        methods: {
          // asdasdasdasdasd(){
          //   console.log("1");
          //   console.log(this.checked);
          // },
          SelectHe(data,index){

            data.select=!data.select;
            if(data.select==false){
              this.AggregateAmount=Number(this.AggregateAmount)+Number(data.jine);
              console.log(this.AggregateAmount);
              this.money++
            }else{
              this.money--;
              this.AggregateAmount=Number(this.AggregateAmount)-Number(data.jine);
            }
          },
        },
        created() {
          this.TallageData.forEach((item,index)=>{
            //如果被选中
            if(item.select==false){
              this.money++;
              this.AggregateAmount=Number(this.AggregateAmount)+Number(item.jine);
            }
          });
          console.log(this.AggregateAmount,this.money);

        }
    }

</script>

<style scoped lang="less">
  .DrawerMessage3{
    .hint{
      height: 1.32rem;background:#F8F2E7 ;padding: .16rem 0.3rem;color: #333;font-size: .24rem;
      a{
        display: inline-block;color: #f90;
      }
    }
    .specific{
      p{
        margin-bottom: .20rem;display:flex;justify-content: space-between;
      }

    }
    .remove{
      font-size: .28rem;text-align: right;height: 1.02rem;line-height:1.02rem;
      p{display: inline-block;text-align: center;height: .62rem;line-height: .62rem;border: 1px solid #f90;width: 1.39rem;color: #ff9900;}
    }
    .bottomBase{
      background-color: #f5f5f5;box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.12);width: 100%;height: 1.12rem;position: fixed;bottom: 0;padding: .23rem .3rem;
      .nextStep{float: right;vertical-align: middle;display: inline-block;background: #f90;line-height: .66rem;height: .66rem;text-align: center;color: #fff;font-size: .28rem;width: 1.62rem}
      div{
        text-align: right;margin-right: .20rem;float: right;line-height: .66rem;
      }

    }
  }
  .icons{
    width: .4rem;
    height: .4rem;
    display: inline-block;
    vertical-align: middle;
  }
  .bg_icon{
    background: url("../../../assets/img/common/Nopitch.png") no-repeat;
    background-size: cover;
  }
  .bg_icons{
    background: url("../../../assets/img/common/pitch.png") no-repeat;
    background-size: cover;
  }

</style>



